/* App.css */

body {
  font-family: 'Arial', sans-serif;
  background-color: #00338d; /* Buffalo Bills royal blue background */
  color: #ffffff; /* White text */
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.App {
  text-align: center;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
  background: linear-gradient(to right, #00338d, #c8102e); /* Buffalo Bills gradient (royal blue to red) */
  width: 90%;
  max-width: 300px;
  margin: 0 auto;
  border: 4px solid #ffffff; /* White border */
  animation: borderAnimation 2s infinite alternate;
}

@keyframes borderAnimation {
  0% {
    border-color: #ffffff;
  }
  100% {
    border-color: #00338d; /* Buffalo Bills royal blue border on animation */
  }
}

button {
  font-size: 16px;
  padding: 15px 20px;
  margin: 10px 0;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  background: linear-gradient(to right, #00338d, #c8102e); /* Buffalo Bills gradient (royal blue to red) */
  color: #ffffff;
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  transform: scale(1); /* Initial scale for dynamic effect */
}

button:hover {
  background: linear-gradient(to right, #c8102e, #870619); /* Darker red on hover */
  transform: scale(1.05); /* Scale up on hover */
}

/* Compact table for numbers rolled */
.counts-container {
  margin-top: 10px; /* Reduced margin */
  max-height: 120px; /* Maximum height for a compact table */
  overflow: auto; /* Enable scrolling for excess content */
}

table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}

th, td {
  padding: 8px;
  border-bottom: 1px solid #ffffff; /* White border */
  font-size: 12px;
}

/* Reset button style */
.reset-button {
  font-size: 16px;
  padding: 15px 20px;
  margin-top: 20px;
  cursor: pointer;
  border: 4px solid #ffffff; /* White border */
  border-radius: 8px;
  background: linear-gradient(to right, #00338d, #c8102e); /* Buffalo Bills gradient (royal blue to red) */
  color: #ffffff;
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  transform: scale(1); /* Initial scale for dynamic effect */
}

.reset-button:hover {
  background: linear-gradient(to right, #c8102e, #870619); /* Darker red on hover */
  transform: scale(1.05); /* Scale up on hover */
}

/* Total rolls style with 3D effect */
.total-rolls {
  margin-top: 20px;
  font-size: 24px;
  color: #ffffff;
  transform: perspective(400px) rotateX(20deg);
}

/* Last rolled style */
.last-rolled {
  margin-top: 10px;
  font-size: 16px;
  color: #ffffff;
}
